    background-color: var(--light-blue);
    opacity: 15%;
    transition: all .6s ease;
    border-radius: var(--border-radius);
}

.menu-button:hover::before {
    opacity: 0;
    transform: scale(0.6, 0.6);
}

.menu-button::after {
