    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0;
    transition: all .6s ease;
    border: 1px solid var(--light-blue);
    transform: scale(1.2, 1.2);
    border-radius: var(--border-radius);
}

.menu-button:hover::after {
    opacity: 1;
    transform: scale(1, 1);
}

.menu-button:active {
    background-color: var(--green);
}

.btn-form {
    width: 214px;
