___CSS_LOADER_EXPORT___.push([module.id, `html {
    box-sizing: border-box;
}
*,
*::before,
::after {
    box-sizing: border-box;
}
p,h1,h2,h3,h4,h5,h6 {
    margin: 0;
}
a {
    display: inline-block;
    color: inherit;
    text-decoration: none;
}

img {
    max-width: 100%;
    word-break: normal;
    object-fit: cover;
}


/* global */

.list-reset {
    margin: 0;
    padding: 0;
    list-style: none;
}
.btn-reset {
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
}

img {
    display: block;
    max-width: 100%;
}

input,
textarea,
select,
button {
    font: inherit;
}

html {
    height: 100%;
    scroll-behavior: smooth;
}


body {
    margin: 0;
    min-height: 100vh;
    line-height: 1.5;
    scroll-behavior: smooth;
    touch-action: none;
    font-size: 16px;
    font-family: 'Overpass', sans-serif;
    font-weight: 400;
    background-color: var(--color-light-blue);
}
  
  
  .dp-fl {
    display: flex;
  }
  
  .grid {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;
    margin: 0 40px;
  }
  
  .section-parameters {
    max-width: 1140px;
    margin: 0 auto;
  
  }
  
  .bg {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
  }
  


